import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
// @mui
import {
    Card,
    Table,
    Button,
    TableRow,
    TableBody,
    TableCell,
    Container,
    TableContainer,
    TableHead,
    Typography,
    Switch,
} from '@mui/material';
import axios from '../../utils/axios';

export default function BankLockInt() {
    const { enqueueSnackbar } = useSnackbar();
    const [providers, setProviders] = useState([]);
    const accessToken = window.localStorage.getItem('accessToken');

    const fetchSettings = async () => {
        try {
            const response = await axios.get(`/api/secure/trans/settings/${accessToken}/habukhan/secure`);
            if (response.data.status === 'success') {
                setProviders(response.data.providers);
            }
        } catch (error) {
            console.error(error);
        }
    };

    useEffect(() => {
        fetchSettings();
    }, []);

    const handleToggleLock = async (slug, currentStatus) => {
        try {
            const action = currentStatus ? 'unlock' : 'lock';
            const response = await axios.post(`/api/secure/lock/bank/${accessToken}/habukhan/secure`, {
                slug,
                action,
            });

            if (response.data.status === 'success') {
                enqueueSnackbar(response.data.message);
                fetchSettings(); // Refresh list
            }
        } catch (error) {
            enqueueSnackbar('Failed to update provider status', { variant: 'error' });
        }
    };

    return (
        <Card sx={{ p: 3 }}>
            <Typography variant="h6" gutterBottom>
                Individual Bank Provider Locks
            </Typography>
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Provider Name</TableCell>
                            <TableCell align="center">Status</TableCell>
                            <TableCell align="right">Action</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {providers.map((row) => (
                            <TableRow key={row.slug}>
                                <TableCell>{row.name}</TableCell>
                                <TableCell align="center">
                                    <Typography
                                        variant="caption"
                                        sx={{
                                            color: row.is_locked ? 'error.main' : 'success.main',
                                            fontWeight: 'bold',
                                        }}
                                    >
                                        {row.is_locked ? 'LOCKED' : 'ACTIVE'}
                                    </Typography>
                                </TableCell>
                                <TableCell align="right">
                                    <Switch
                                        checked={!row.is_locked}
                                        onChange={() => handleToggleLock(row.slug, row.is_locked)}
                                        color="success"
                                    />
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Card>
    );
}
